.mainText{width:600px;margin:0 auto;margin-bottom:100px;height:100px}.mainText div{width:70%;margin:0 auto;display:flex;justify-content:center;align-items:center;text-align:center}.mainText div p{width:100%;margin:0 auto;text-align:center;font-size:1.84vh;font-weight:bold;color:#776b5d;display:inline-block;position:relative;line-height:2}.mainText div p::after{content:"ㅣ";position:absolute;animation:blink 1s infinite}@keyframes blink{0%,50%{opacity:1}50%,100%{opacity:0}}.mainImg{width:100%;margin:0 auto}.mainImg img{width:70%}.mainMenu{width:100%;margin:10px auto}.mainMenu ul{width:100%;display:flex;justify-content:space-evenly;align-items:center;margin:0 auto;padding:0}.mainMenu ul li{list-style:none;margin:0 auto;display:flex;padding:20px;align-items:center;justify-content:center}.mainMenu ul li a{text-decoration:none;color:#776b5d;font-size:1.5rem;font-weight:600;padding:10px 20px;transition:all 1s ease-in-out;border-radius:10%;display:flex;flex-direction:column;justify-content:center;align-items:center}.mainMenu ul li a:hover{background-color:#776b5d;color:#ebe3d5}.mainMenu ul li a .imgDiv{width:150px;height:150px;margin-top:10px}.mainMenu ul li a .imgDiv img{width:100%;height:100%;border-radius:20px;box-shadow:0px 0px 10px #776b5d}.nameDiv{text-align:center}@media(max-width: 768px){.mainImg{display:none}.mainMenu ul{flex-direction:column !important}}